import WALLETS from "./WalletTypes";

const INITIAL_STATE = {
   WalletItems:[]
}

const walletReducer = (state=INITIAL_STATE, action) => {
    switch (action.type) {
        case WALLETS.ADD_WALLET:
            return {
                WalletItems: [action.payload, ...INITIAL_STATE.WalletItems],
            };
        default:
            return state;
      
    }
    console.log(state)

}
export default walletReducer;